import React, { useState, useEffect } from 'react'
import Names from './components/Names'
import Filter from './components/Filter'
import axios from 'axios'
import personService from './services/Persons'


const Notification = ({ message }) => {

  if (message === null) {
    return null
  }

  const style = {
    "border-color": message.isError ? "red" : "green",
    "fontSize": "16px",
    "borderStyle": "solid",
    "borderRadius": "3px",
    "padding": "10px",
    "marginBottom": "5px"
  }

  return (
    <div style={style}>
      {message.content}
    </div>
  )
}


const App = () => {
  const [ message, setMessage ] = useState(null)
  const [ persons, setPersons] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNum, setNewNum ] = useState('')
  const [ filter, setFilter ] = useState('')


  useEffect(() => {
    axios
      .get('/api/persons')
      .then(response => {
        setPersons(response.data)
      })
  }, [])


  const addNameAndNum = (event) => {
    event.preventDefault()
    if (persons.some(e => e.name === newName)) {
          console.log("On jo listassa")
          setNewName('')
          setNewNum('')
          const acceptModification = window.confirm(
            `${newName} on jo listassa, päivitetäänkö numero?`
            )
    
          if (acceptModification) {
            return handleUpdate(newName)
          }
          else return window.alert('Ei muutoksia')
        }


    const noteObject = {
        name: newName,
        num: newNum,
        id: persons.length + 1,
      }
    
      handleCreate()
      setPersons(persons.concat(noteObject))
      setNewName('')
      setNewNum('')
  }


  const handleUpdate = (name) => {
    console.log("mennäänkö tänne kuitenki")
    const oldPerson = persons.find(p => p.name === name)
    const updatedPerson = {...oldPerson, number: newNum}

    personService
      .update(updatedPerson.id, updatedPerson)
      .then(returnedPerson => {
        setPersons(
          persons.map(
            person =>
              person.id !== oldPerson.id ? person: returnedPerson
            )
          )
        setMessage({content:`Päivitetty ${updatedPerson.name}.`, isError: false})
        })
      .catch(error => {
        if (error.message === "Request failed with status code 404"){
          setMessage({content: `${name} tiedot on jo poistettu`, isError: true})
        } else {
          console.log("error message: " + error.message)
          setMessage({content: error.response.data.error, isError: true})
        }
      })
  }


  const handleCreate = () => {
    const newPerson = {
      name: newName,
      number: newNum
    }

    personService
      .create(newPerson)
      .then(newPerson => {
        setPersons(persons.concat(newPerson))
        setMessage({content: `Luotu ${newPerson.name}.`, isError: false})
      })
      .catch(error => {
        setMessage({content: error.response.data.error, isError: true})
      })

    setNewName('')
    setNewNum('')
  }


  const handleDeletePerson = (name, id) => {
    return () => {
      if (window.confirm(`Poistetaanko ${name} ?`)) {
        personService
          .deletePerson(id)
          .then(() => {
            setPersons(persons.filter(n => n.id !== id));
            setMessage({content: `Poistettu ${name}.`, isError: false})
            setNewName("");
            setNewNum("");
          })
          .catch(error => {
            setPersons(persons.filter(n => n.name !== name));
            setMessage({content: `${name} on jo poistettu.`, isError: true})
          });
        setTimeout(() => {
          setMessage(null);
        }, 3000);
      }
    }
  }



  const handleNameChange = (event) => {
    console.log(event.target.value)
    setNewName(event.target.value)
  }

  const handleNumChange = (event) => {
    console.log(event.target.value)
    setNewNum(event.target.value)
  }

  const handleFilterChange = (event) => setFilter(event.target.value)


  return (
    <div>
      <Filter filter={filter} onFilterChange={handleFilterChange}/>
      <h2>Puhelinluettelo</h2>
      <Notification message={message} />
      <form onSubmit={addNameAndNum}>
        <div>
          nimi:
          <input
          value={newName}
          onChange={handleNameChange}
        />
        </div>
        <div>numero: 
            <input 
            value={newNum}
            onChange={handleNumChange}
            />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
      <h2>Numerot</h2>
      <ul>
      <Names
        filter={filter}
        persons={persons}
        handleDeletePerson={handleDeletePerson}
      />
      </ul>
    </div>
  )

}

export default App