import React from "react"
import Name from "./Name"

const Names = ({ filter, persons, handleDeletePerson}) => {
      const caseinsensitiveFilter = (person) => (
      person.name.toUpperCase().includes(
        filter.toUpperCase()
      )
    )
    return (
      <ul>
        {persons
          .filter(caseinsensitiveFilter)
          .map(person => <Name key={person.name} name={person.name} id={person.id} person={person} handleDeletePerson={handleDeletePerson(person.name, person.id)}/>)}
      </ul>
    )
}

  export default Names